<template>
  <md-tabs>
    <md-tab id="tab-games" md-label="Игры">
      <div class="games md-content">
        <div class="md-layout">
          <div class="md-layout-item">
            <md-table
              v-model="games"
              md-sort="id"
              md-sort-order="desc"
              md-card
              md-fixed-header
              @md-selected="onSelect"
            >
              <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="single">
                <md-table-cell md-label="№" md-numeric md-sort-by="id">{{ item.id }}</md-table-cell>
                <md-table-cell md-label="Дата" md-sort-by="date" class="date">{{ item.date }}</md-table-cell>
                <md-table-cell md-label="1">{{ getPlayerName(item.players[0]) }}</md-table-cell>
                <md-table-cell md-label="2">{{ getPlayerName(item.players[1]) }}</md-table-cell>
                <md-table-cell md-label="3">{{ getPlayerName(item.players[2]) }}</md-table-cell>
                <md-table-cell md-label="4">{{ getPlayerName(item.players[3]) }}</md-table-cell>
                <md-table-cell md-label="5">{{ getPlayerName(item.players[4]) }}</md-table-cell>
              </md-table-row>
            </md-table>
            <div class="md-layout">
              <div class="md-layout-item md-size-33">
                <md-field>
                  <label>Файл с данными игр</label>
                  <md-file @md-change="importDatabase($event)" accept="application/json" />
                </md-field>
              </div>
              <div class="md-layout-item">
                <md-button class="md-raised first-button" @click="exportDatabase">Выгрузить базу</md-button>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <md-button class="md-raised" @click="saveToStorage">Сохранить базу</md-button>
                <md-button class="md-raised" @click="loadFromStorage">Загрузить базу</md-button>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item md-size-33">
                <v-menu
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="databaseSettings.gamesMonth"
                      label="Месяц игр"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="databaseSettings.gamesMonth"
                    no-title
                    type="month"
                    locale="ru"
                  ></v-date-picker>
                </v-menu>
              </div>
              <div class="md-layout-item">
                <md-button class="md-raised" @click="newMonth">Новый месяц</md-button>
              </div>
            </div>
          </div>
          <div class="md-layout-item game">
            <div class="md-layout game-header">
              <div class="md-layout-item" v-if="selected.id">Игра №{{selected.id}}</div>
              <div class="md-layout-item" v-if="!selected.id">Новая Игра</div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[0]"
                  :index="0"
                  :players="players"
                  :selectedRole="selectedRoles[0]"
                  @selectedRole="updateSelectedRole($event, 0)"
                ></PlayerSelector>
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[1]"
                  :index="1"
                  :players="players"
                  :selectedRole="selectedRoles[1]"
                  @selectedRole="updateSelectedRole($event, 1)"
                ></PlayerSelector>
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[2]"
                  :index="2"
                  :players="players"
                  :selectedRole="selectedRoles[2]"
                  @selectedRole="updateSelectedRole($event, 2)"
                ></PlayerSelector>
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[3]"
                  :index="3"
                  :players="players"
                  :selectedRole="selectedRoles[3]"
                  @selectedRole="updateSelectedRole($event, 3)"
                ></PlayerSelector>
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[4]"
                  :index="4"
                  :players="players"
                  :selectedRole="selectedRoles[4]"
                  @selectedRole="updateSelectedRole($event, 4)"
                ></PlayerSelector>
              </div>
              <div class="md-layout-item">
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[5]"
                  :index="5"
                  :players="players"
                  :selectedRole="selectedRoles[5]"
                  @selectedRole="updateSelectedRole($event, 5)"
                ></PlayerSelector>
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[6]"
                  :index="6"
                  :players="players"
                  :selectedRole="selectedRoles[6]"
                  @selectedRole="updateSelectedRole($event, 6)"
                ></PlayerSelector>
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[7]"
                  :index="7"
                  :players="players"
                  :selectedRole="selectedRoles[7]"
                  @selectedRole="updateSelectedRole($event, 7)"
                ></PlayerSelector>
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[8]"
                  :index="8"
                  :players="players"
                  :selectedRole="selectedRoles[8]"
                  @selectedRole="updateSelectedRole($event, 8)"
                ></PlayerSelector>
                <PlayerSelector
                  v-if="renderComponent"
                  v-model="selected.players[9]"
                  :index="9"
                  :players="players"
                  :selectedRole="selectedRoles[9]"
                  @selectedRole="updateSelectedRole($event, 9)"
                ></PlayerSelector>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <v-menu
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field v-model="selected.date" label="Дата игры" readonly v-on="on"></v-text-field>
                  </template>
                  <v-date-picker v-model="selected.date" no-title locale="ru"></v-date-picker>
                </v-menu>
              </div>
              <div class="md-layout-item">
                <label>Первый убитый</label>
                <model-list-select
                  :list="selectPlayerOptions"
                  v-model="selected.firstKilled"
                  option-value="index"
                  option-text="label"
                ></model-list-select>
              </div>
              <div class="md-layout-item">
                <label>Лучший ход</label>
                <model-list-select
                  :list="firstKilledGuessPointsOptions"
                  v-model="selected.firstKilledGuessPoints"
                  option-value="value"
                  option-text="label"
                ></model-list-select>
              </div>
              <div class="md-layout-item">
                <label>Победа</label>
                <model-list-select
                  :list="winGameOptions"
                  v-model="selected.cityWin"
                  option-value="value"
                  option-text="label"
                ></model-list-select>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <md-button class="md-raised first-button" @click="setControlsToNewGame">Новая игра</md-button>
              </div>
              <div class="md-layout-item edit-buttons">
                <md-button class="md-raised md-primary" @click="saveGame">Сохранить</md-button>
                <md-button class="md-raised last-button" @click="reloadEdit">Сбросить</md-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </md-tab>
    <md-tab id="tab-players" md-label="Игроки">
      <Players
        :players="players"
        :games="games"
        :databaseSettings="databaseSettings"
        @removePlayer="removePlayer($event)"
      />
    </md-tab>
  </md-tabs>
</template>

<script >
import { ModelListSelect } from "vue-search-select";
import Players from "./Players.vue";
import PlayerSelector from "./PlayerSelector.vue";
import "@mdi/font/css/materialdesignicons.css";

export default {
  name: "Games",
  components: {
    ModelListSelect,
    Players,
    PlayerSelector
  },
  data: () => ({
    renderComponent: true,
    databaseFile: { name: "" },
    filename: null,
    selectedDate: null,
    selected: {
      id: null,
      date: null,
      players: Array(10).fill(null),
      don: null,
      maf1: null,
      maf2: null,
      sherif: null,
      cityWin: null
    },
    selectPlayerOptions: [
      { index: 0, label: "1" },
      { index: 1, label: "2" },
      { index: 2, label: "3" },
      { index: 3, label: "4" },
      { index: 4, label: "5" },
      { index: 5, label: "6" },
      { index: 6, label: "7" },
      { index: 7, label: "8" },
      { index: 8, label: "9" },
      { index: 9, label: "10" }
    ],
    selectedRoles: Array(10).fill("citizen"),
    firstKilledGuessPointsOptions: [
      { value: 0, label: "0" },
      { value: 0.25, label: "0.25" },
      { value: 0.4, label: "0.4" }
    ],
    winGameOptions: [
      { value: true, label: "Мир" },
      { value: false, label: "Мафия" }
    ],
    databaseSettings: {
      gamesMonth: "",
      maxPlayerId: 0,
      maxGameId: 0
    },
    players: [],
    games: []
  }),
  created: function() {},
  methods: {
    updateSelectedRole($event, index) {
      this.selectedRoles[index] = $event;
      this.$forceUpdate();
    },
    forceRerender() {
      // Remove my-component from the DOM
      this.renderComponent = false;

      this.$nextTick(() => {
        // Add the component back in
        this.renderComponent = true;
      });
    },
    getEmptySelectedRoles() {
      return Array(10).fill("citizen");
    },
    getSelectedRoles(game) {
      var result = Array(10).fill("citizen");

      result[game.don] = "don";
      result[game.sherif] = "sherif";
      result[game.maf1] = "maf";
      result[game.maf2] = "maf";
      return result;
    },
    onSelect(item) {
      this.selected = this.copyGame(item);
      this.selectedRoles = this.getSelectedRoles(item);
    },
    newMonth() {
      this.games = [];
      this.databaseSettings.gamesMonth = new Date().toISOString().substr(0, 7);
      this.databaseSettings.maxGameId = 0;
      this.setControlsToNewGame();
    },
    getDatabase() {
      return {
        formatVersion: 1,
        settings: this.databaseSettings,
        games: this.games,
        players: this.players
      };
    },
    setDatabase(database) {
      this.games = database.games;
      this.players = database.players;
      this.databaseSettings = database.settings;
    },
    getPlayerName(id) {
      if (!id) {
        return "";
      }
      return this.players.find(x => x.id === id).name;
    },
    getEmptyGame() {
      return {
        id: null,
        date: null,
        players: Array(10).fill(null),
        don: null,
        maf1: null,
        maf2: null,
        sherif: null,
        firstKilled: null,
        firstKilledGuessPoints: null,
        cityWin: null
      };
    },
    copyGame(game) {
      return {
        id: game.id,
        date: game.date,
        players: game.players.slice(),
        don: game.don,
        maf1: game.maf1,
        maf2: game.maf2,
        sherif: game.sherif,
        firstKilled: game.firstKilled,
        firstKilledGuessPoints: game.firstKilledGuessPoints,
        cityWin: game.cityWin
      };
    },
    isSelectedRolesValid() {
      if (this.selectedRoles.filter(x => x == "don").length != 1) {
        return false;
      }
      if (this.selectedRoles.filter(x => x == "sherif").length != 1) {
        return false;
      }
      if (this.selectedRoles.filter(x => x == "maf").length != 2) {
        return false;
      }
      return true;
    },
    saveGame() {
      if (!this.isSelectedRolesValid()) {
        alert("Должно быть два мафа, дон и шериф");
        return;
      }

      if (this.selected.id) {
        var game = this.games.find(x => x.id === this.selected.id);
        game.date = this.selected.date;
        game.players = this.selected.players;
        game.firstKilled = this.selected.firstKilled;
        game.firstKilledGuessPoints = this.selected.firstKilledGuessPoints;
        game.cityWin = this.selected.cityWin;

        game.don = this.selectedRoles.indexOf("don");
        game.sherif = this.selectedRoles.indexOf("sherif");
        game.maf1 = this.selectedRoles.indexOf("maf");
        game.maf2 = this.selectedRoles.indexOf("maf", game.maf1 + 1);

        this.selected = this.copyGame(game);
        this.selectedRoles = this.getSelectedRoles(game);
      } else {
        this.databaseSettings.maxGameId++;
        this.selected.id = this.databaseSettings.maxGameId;

        this.selected.don = this.selectedRoles.indexOf("don");
        this.selected.sherif = this.selectedRoles.indexOf("sherif");
        this.selected.maf1 = this.selectedRoles.indexOf("maf");
        this.selected.maf2 = this.selectedRoles.indexOf(
          "maf",
          this.selected.maf1 + 1
        );

        this.games.push(this.selected);
        this.selected = this.getEmptyGame();
        this.selectedRoles = this.getEmptySelectedRoles();
      }
    },
    setControlsToNewGame() {
      this.selected = this.getEmptyGame();
      this.selectedRoles = this.getEmptySelectedRoles();
    },
    reloadEdit() {
      if (this.selected.id) {
        var game = this.games.find(x => x.id === this.selected.id);
        this.selected = this.copyGame(game);
        this.selectedRoles = this.getSelectedRoles(game);
      } else {
        this.selected = this.getEmptyGame();
        this.selectedRoles = this.getEmptySelectedRoles();
      }
    },
    exportDatabase() {
      var content = JSON.stringify(this.getDatabase());
      var fileName = this.databaseSettings.gamesMonth + "-games.json";
      this.downloadFile(content, fileName, "application/json");
    },
    downloadFile(content, fileName, contentType) {
      var a = document.createElement("a");
      var file = new Blob([content], { type: contentType });
      a.href = URL.createObjectURL(file);
      a.download = fileName;
      a.click();
    },
    test() {},
    importDatabase(e) {
      var reader = new FileReader();
      var self = this;
      reader.onload = function() {
        var database = JSON.parse(reader.result);
        self.setDatabase(database);
        this.setControlsToNewGame();
      };
      reader.readAsText(e[0]);
    },
    saveToStorage() {
      window.localStorage.setItem(
        "database",
        JSON.stringify(this.getDatabase())
      );
    },
    loadFromStorage() {
      var database = JSON.parse(window.localStorage.getItem("database"));
      this.setDatabase(database);
      this.setControlsToNewGame();
    },
    removePlayer(playerId) {
      this.players = this.players.filter(x => x.id != playerId);
    }
  }
};
</script>

<style lang="scss" scoped>
::v-deep .md-tabs-content {
  height: calc(100vh - 50px) !important;
}

::v-deep .md-tab {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.md-layout-item {
  padding-left: 5px;
  padding-right: 5px;
  .search.selection.dropdown {
    margin-bottom: 3px;
  }
}

.game {
  .mx-datepicker {
    margin-top: 24px;
  }

  .game-header {
    text-align: center;
    font-size: 1.3em;
    font-weight: 700;
    margin-bottom: 10px;
  }

  .edit-buttons {
    text-align: right;
  }
  .first-button {
    margin-left: 0;
  }
  .last-button {
    margin-right: 0;
  }
}

.game {
  .role {
    width: 70px;
  }
}

::v-deep .v-text-field__details {
  margin-bottom: 0px !important;
}
</style>
