<template>
  <div class="d-flex">
    <v-autocomplete
      :value="value"
      @input="$emit('input', $event)"
      :items="players"
      item-value="id"
      item-text="name"
      filled
      dense
      :label="label"
      class="flex-grow-1 mr-1"
    ></v-autocomplete>
    <v-select
      :value="selectedRole"
      @input="$emit('selectedRole', $event)"
      :items="selectRoleOptions"
      filled
      dense
      label="Роль"
      class="flex-shrink-0 role"
    ></v-select>
  </div>
</template>

<script >
export default {
  name: "PlayerSelector",
  props: {
    index: Number,
    value: [Object, Number],
    selectedRole: [Object, String],
    players: Array
  },
  components: {},
  data: () => ({
    label: "",
    selectRoleOptions: [
      { value: "citizen", text: "М" },
      { value: "maf", text: "Маф" },
      { value: "don", text: "Дон" },
      { value: "sherif", text: "Шер" }
    ]
  }),
  created: function() {
    this.label = "Игрок " + (this.index + 1);
  }
};
</script>

<style lang="scss" scoped>
.role {
  width: 70px;
}
</style>
