<template>
  <div class="players md-content">
    <div class="md-layout" v-if="mounted">
      <div class="md-layout-item md-size-35">
        <v-text-field v-model="search" label="Поиск игрока" single-line hide-details></v-text-field>
        <br />
        <v-layout column style="height: 80vh">
          <v-flex style="overflow: auto">
            <v-data-table
              :headers="headers"
              :items="players"
              :search="search"
              :sort-by="['name']"
              :sort-desc="[false]"
              disable-pagination
              hide-default-footer
              must-sort
              @click:row="onSelect"
            >
              <template v-slot:item.name="{ item }">{{ item.name }}</template>
            </v-data-table>
          </v-flex>
        </v-layout>
      </div>
      <div class="md-layout-item">
        <div class="md-layout player-edit-header">
          <div class="md-layout-item" v-if="selectedPlayer">{{selectedPlayer.name}}</div>
          <div class="md-layout-item" v-if="!selectedPlayer">*Новый игрок*</div>
        </div>
        <div class="md-layout previous-names">
          <div class="md-layout-item md-size-50">
            <h4>Предыдущие ники</h4>
            <md-table
              v-if="selectedPlayer"
              v-model="selectedPlayer.nameHistory"
              md-sort="date"
              md-sort-order="desc"
              md-card
              md-fixed-header
            >
              <md-table-row slot="md-table-row" slot-scope="{ item }" md-selectable="single">
                <md-table-cell md-label="Ник" md-sort-by="name">{{ item.name }}</md-table-cell>
                <md-table-cell
                  md-label="Дата смены"
                  md-sort-by="name"
                >{{ new Date(item.date).toISOString().slice(0,10) }}</md-table-cell>
              </md-table-row>
            </md-table>
          </div>
          <div class="md-layout-item">
            <div class="md-layout">
              <div class="md-layout-item">
                <md-field>
                  <label>{{ this.selectedPlayer ? "Сменить ник" : "Введите ник" }}</label>
                  <md-input v-model="nameEditModel"></md-input>
                </md-field>
              </div>
            </div>
            <div class="md-layout">
              <div class="md-layout-item">
                <md-button class="md-raised first-button" @click="newPlayer">Новый игрок</md-button>
              </div>
              <div class="md-layout-item-66 edit-buttons">
                <md-button
                  class="md-raised md-primary"
                  :disabled="nameEditModel==''"
                  @click="savePlayer"
                >Сохранить</md-button>
                <md-button
                  class="md-raised md-accent last-button"
                  :disabled="!selectedPlayer"
                  @click="removePlayer"
                >Удалить</md-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script >
export default {
  name: "Players",
  props: { players: Array, games: Array, databaseSettings: Object },
  data: () => ({
    //this variable workaround. Without v-if condition with it default sorting isn't work
    mounted: null,
    selectedPlayer: null,
    nameEditModel: "",
    search: "",
    headers: [{ text: "Игрок", value: "name" }]
  }),
  mounted() {
    this.mounted = true;
  },
  methods: {
    onSelect(item) {
      this.selectedPlayer = item;
      if (item && !item.nameHistory) {
        item.nameHistory = [
          {
            name: item.name,
            date: Date.now()
          }
        ];
      }
    },
    newPlayer() {
      this.selectedPlayer = null;
      this.nameEditModel = "";
    },
    savePlayer() {
      if (this.selectedPlayer) {
        this.selectedPlayer.name = this.nameEditModel;
        this.selectedPlayer.nameHistory.push({
          nameChangeId: this.selectedPlayer.nameHistory.length + 1,
          name: this.nameEditModel,
          date: Date.now()
        });
        this.nameEditModel = "";
      } else {
        this.databaseSettings.maxPlayerId++;
        var newPlayer = {
          id: this.databaseSettings.maxPlayerId,
          name: this.nameEditModel,
          nameHistory: [
            {
              nameChangeId: 1,
              name: this.nameEditModel,
              date: Date.now()
            }
          ]
        };
        this.players.push(newPlayer);

        this.nameEditModel = "";
        this.selectedPlayer = newPlayer;
      }
    },
    removePlayer() {
      var gameIdsWithPlayer = [];
      var playerIdToDelete = this.selectedPlayer.id;
      this.games.forEach(function(game) {
        if (game.players.includes(playerIdToDelete)) {
          gameIdsWithPlayer.push(game.id);
        }
      });
      if (gameIdsWithPlayer.length > 0) {
        var message =
          "Невозможно удалить игрока так как он участвует в играх с номерами: " +
          gameIdsWithPlayer.join(", ");
        alert(message);
      } else {
        var confirmResult = confirm("Вы уверены что хотите удалить игрока?");
        if (confirmResult) {
          this.$emit("removePlayer", playerIdToDelete);
          this.newPlayer();
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
::v-deep .md-tabs-content {
  height: calc(100vh - 50px) !important;
}

::v-deep .md-tab {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.previous-names td.md-table-cell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  min-width: 200px;
  max-width: 200px;
}

.md-layout-item {
  padding-left: 20px;
  padding-right: 20px;
  .search.selection.dropdown {
    margin-bottom: 3px;
  }
}

.players {
  .mx-datepicker {
    margin-bottom: 3px;
  }

  .player-edit-header {
    text-align: center;
    font-size: 1.3em;
    font-weight: 700;
    margin-bottom: 10px;
  }
  .edit-buttons {
    text-align: right;
  }
  .first-button {
    margin-left: 0;
  }
  .last-button {
    margin-right: 0;
  }
}
</style>
